*, *::before, *::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  margin: 0;
  background: url("/img/blue.jpg") no-repeat center center fixed; /*ADD BROWSER COMPATIBLITIES*/
  background-size: cover;
  color: white;
}

main {
  display: flex;
  flex-direction: column;

}

header {
  display: flex;
  position: sticky;
  top: 0;
  padding: 0 1rem 0 1rem;
  justify-content: flex-end;
  background-color: #021672;
  z-index: 100;
  /* align-items: flex-end; */ /* this will move bar menu to the bottom*/
}

.logo {
  flex-grow: 3;
}

.nav-container {
  flex-grow: 1;
}

.nav-toggle {
  display: none;
}

.nav-toggle-label {
  position: absolute;
  top: 0;
  right: 0;
  margin-right: 1em;
  height: 100%;
  display: flex;
  align-items: center;
}

.nav-toggle-label span,
.nav-toggle-label span::before,
.nav-toggle-label span::after {
  display: block;
  background: white;
  height: 2px;
  width: 2em;
  border-radius: 2px;
  position: relative;
}

.nav-toggle-label span::before,
.nav-toggle-label span::after {
  content: '';
  position: absolute;
}
  
.nav-toggle-label span::before {
  bottom: 7px;
}

.nav-toggle-label span::after {
  top: 7px;
}

nav {
  position: absolute;
  text-align: right;
  top: 100%;
  right: 0;
  width: 100%;
  display: none;
}


nav ul li a {
  margin: 0;
  padding: 0;
  margin-bottom: 1em;
  margin-right: 1em;
  text-decoration: none;
  color: white;
}

nav ul {
  list-style-type: none;
}

h1:hover {
  color: #0478DF;
}


a:hover {
  color: #0478DF;
  text-shadow: 1px 0.5px white;
  text-decoration: none;
}

.nav-toggle:checked ~ nav {
  display: block;
}

.nav-stack {
  display: flex;
  flex-direction: column;
}

h1 {
  letter-spacing: 0.5em;
  text-shadow: 3px 2px silver;
}

h2 {
  letter-spacing: 0.5em;
  text-shadow: 1px 2px gray;
}


section {
