}

.contact {
  width: 0;
  position: relative;
  animation: mymove 5s;
  }

@keyframes mymove {
  0%   {bottom: 75px;}
  25% {bottom: 200px;}
  75%  {bottom: 50px}
  100% {bottom: 0px;}
}